	module.exports = {"data":{"markdownRemark":{"html":"<p>米国がなぜ中国に最も好都合な国家処理プロセスを与えたのかを分析するために、米国国際ヘラルドトリビューンの記事を見てください。アメリカ人は技術的すぎる、特に政治的な芸術を理解していない、非常に実用的です。前項では、アメリカの学界や政界の基本的な判断は、おそらく中国があまり役に立たず、景気の見通しがないと信じていた可能性がありますが、政治的な過渡期であったため、私はさまざまな圧力によって中国の政策を変えたいと思う。しかし、国の政策は、国の基本的利益や社会の発展の道と結びついていることが多く、自由に変更することはできない。アメリカの戦術はさまざまな技術的方法で行われています。中国文化は伝統的にこのように政治を扱っていないが、政治芸術、特に敏感な問題に重点を置いている。中国の経済が発展した後、中国が譲歩を余儀なくされていないとき、米国が中国を必要とするとき、アメリカ人は譲歩する余裕がない。米国が譲歩を望んでいるとき、それはまだ措置を模索しており、結果はまだ技術主義または実用主義である。このようにして、大きな東部の国に対処することは効果的ではありません。米国はアジアで多くの教訓を持ち、アメリカ人はおそらくゆっくりと学ぶでしょう。</p>\n<p>政治はしばしば2つの視点を持っています.1つは技術主義であり、最も典型的なものであり、もう1つは芸術であり、アジアの一部の国が最も典型的です。文化や歴史、特に人々の精神的な世界を深く理解できない場合は、コンセンサスに達することは容易ではありません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0108.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0108.md","frontmatter":{"title":"6月7日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0108.md absPath of file >>> MarkdownRemark"}}

